import React from "react"
// import Chart from "frappe-charts/dist/frappe-charts.min.esm"
import Chart from 'app/lib/chart/js/charts'

class Comp extends React.Component {
  constructor(props) {
    super(props);
    const {caption, fields, data, colors} = props;


    const polished = data.reduce((p, v) => {

      p.labels.push(v.date);
      fields.forEach(({prop, propTitle, propFormat}) => {
        const values = p.datasets[propTitle] = p.datasets[propTitle] || [];
        values.push(propFormat(v[prop]));
      });
      return p;

    }, {labels: [], datasets: {}});

    this.state = {
      title: caption,
      colors,
      data: {

        labels: polished.labels,

        datasets: Object.entries(polished.datasets).reduce((p, [title, values]) => {
          p.push({
            title,
            values
          });
          return p;
        }, [])
      }
    };
  }

  componentDidMount() {
    this._chart = new Chart({
      parent: this.$chart, // or a DOM element
      ...this.state,
      type: 'bar',
      height: 250,

      format_tooltip_x: d => (d + '').toUpperCase(),
      format_tooltip_y: d => d + ''
    });
  }

  componentWillUnmount() {
    this._chart.destroy();
  }


  render() {
    return <div ref={el => this.$chart = el}>

    </div>
  }
}


export default Comp
